<template>
  <div>
    <header>
      <nav>
        <div class="text-title">
            VueJS Grocery List
        </div>
      </nav>
    </header>
    <section class="table">
      <div class="table_head">
        <div class="head-title">
          <div class="table_head-title">Your Current List</div>
        </div>
        <div class="images-wrapper">
            <img src="./static/images/saveIcon.png" alt="" @click="save">
            <img src="./static/images/bdlogo.jpg" alt="" @click="retrieve">
            <img src="./static/images/printerLogo.jpg" alt="" @click="imprimir">
        </div>
      </div>
      <div class="table_middle" v-if="groceryList.length>0">
        <div class="table-item" v-for="(item,index) in groceryList" :key=index>
          <input type="text" v-model="item.name">
          <input type="number" min="1" v-model="item.quantity">
          <button class="reload">&#8635;</button>
          <button class="delete" @click="deleteItem(index)">X</button>
        </div>
      </div>
      <div class="table_end">
        <div class="table_end_wrapper">
          <input ref="entrada" type="text" placeholder="New item...">
          <input ref="num" type="number" value="1" min="1">
          <button class="add" @click="addItem">ADD</button>
        </div>
      </div>
    </section>  

  </div>  
</template>

<script>
import axios from 'axios';
export default {
  data () {
    return {
      groceryList:[
        {
          'name':'Milk',
          'quantity':1
        },
        {
          'name':'Tomatoes',
          'quantity':5
        }
      ]
    }
  },
  methods:{
    deleteItem(index){
      this.groceryList.splice(index,1);
    },
    addItem(name,quantity){
      let input = this.$refs.entrada.value;
      let cant = this.$refs.num.value;
      
      if (input.length>0){
        this.groceryList.push({
          'name':input,
          'quantity':cant
        });
      this.$refs.entrada.value='';
      this.$refs.num.value=1;
      }
    },
    imprimir(){
      var dd = {
        content: [],
        styles: {
            f18: {
                fontSize: 18
            },
            strong: {
                bold: true
            }
        }
      };
      dd.content.push("YOUR GROCERY LIST: \n\n");
      this.groceryList.forEach((el)=>{
        dd.content.push(el.name+" X"+el.quantity);
      })
      
      pdfMake.createPdf(dd).download('groceries.pdf');
    },
    save:function(){
      axios.put('https://grocerylist-fc52e.firebaseio.com/list.json',this.groceryList)
      .then((res)=> console.log(res))
      .catch(error=>console.log(error))
    },
    retrieve(){
      axios.get('https://grocerylist-fc52e.firebaseio.com/list.json')
      .then((res)=>{
        console.log(res)
        const data = res.data
        if (data === null){
          console.log('DATA NULL');
          
        }
        else this.groceryList = data;
      })
      .catch(error=>console.log(error))
    }

  }
}
</script>

<style>

body {
  margin:0;
	width: 100wh;
	height: 100vh;
	color: #fff;
	background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
	background-size: 400% 400%;
	-webkit-animation: Gradient 15s ease infinite;
	-moz-animation: Gradient 15s ease infinite;
	animation: Gradient 15s ease infinite;
  
}

@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@-moz-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}
nav{
  height:60px;
  background: rgb(68, 67, 67);
  border-bottom:1px solid black;
  display:flex;
  flex-direction: column;
  justify-content: center;
}
.text-title{
   font-family: 'Bangers', cursive;
   left:10px;
   position:absolute;
   font-size:2rem;
   text-shadow: 0 0 5px rebeccapurple;
}
.table{
  position:absolute;
  margin-top:50px;
  left:25%;
  width:50%;
  height:auto;
  background: #fafafa;
  
}

.table_head{
  border:2px solid black;
  border-bottom:none;
  border-radius: 3%;
  height:2.5rem;
  display:flex;
  flex-direction: column;
  justify-content: center;
  position:relative;
  flex-wrap: wrap;
}
.images-wrapper{
  display:flex;
  justify-content: flex-end;
  padding-right: 10px;
}
.images-wrapper img{
  height:30px;
  right:1px;
  cursor: pointer;
  
}
.table_head-title{
  color:red;
  font-size: 1.5rem;
  font-family: 'Kaushan Script', cursive;
  left:10px;
  position:relative;
  text-shadow: 0 1px 1px black;
}

.table_middle{
  border:2px solid black;
  height:80%;
  text-align: center;
  border-bottom:none;
  padding-bottom:0.5rem;
}


.table-item{
  margin-top:0.5rem;
  color:black;
  left:10%;
  
}
.reload{
  font-weight: 3rem;
  background:greenyellow;
  border-radius: 25%;
  height:1.5rem;
  width:2rem;
  border: 2px solid green;
}

.delete{
  font-weight: 3rem;
  background:red;
  color:white;
  border-radius: 20%;
  border: 2px solid brown;
  height:1.5rem;
  width:2rem;
}
input[type=text]{
  background:#fafafa;
  border-radius: 5%;
  border: 2px solid gray;
  font-family: 'Cabin', sans-serif;
  font-size: 1rem;
  width:13rem;
}
input[type=number]{
  width:2rem;
  border: 2px solid gray;
  border-radius: 5%;
  background:#fafafa;
  font-size:1rem;
  font-family: 'Cabin',sans-serif;
  padding-left:3px;
}
.table_end{
  border:2px solid black;
  height:2.5rem;
  border-radius: 4%;
}
.table_end_wrapper{
  text-align: center;
  padding-top:0.5rem;
}

.add{
  border: 2px solid blue;
  background-color:cyan;
  border-radius: 12%;
  color:blue;
  font-weight: bold;
  padding: 0.15rem 0.5rem;
}


@media(max-width:850px){
  .table{
    
    left:10%;
    width:80%;
  }
  .text-title{
   left:50px;
   font-size:2rem;
  }
}

@media(max-width:520px){
  .table{
    
    left:2%;
    top:5%;
    width:96%;
   
  }
  nav{
    align-content: center;
    align-items: center;
  }
  .text-title{
   position:relative;
   font-size:2rem;
   left:0;
  }
  .table-item{
  display:flex;
  align-items: center;
  justify-content:space-evenly;
  }
  .table_end_wrapper{
  display:flex;
  align-items: center;
  justify-content:space-evenly;
}
}
</style>



